.feedback-form {
  max-width: 480px;
  background-color: #d8bfd8;
  border: 3px solid #7fffd4;
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  padding: 12px;
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

